import { Component, Input } from '@angular/core';
import { GameWrapper } from '../../models/game-wrapper.model';
import { NavController } from 'ionic-angular';

@Component({
  selector: 'game-thumbnail-component',
  templateUrl: 'game-thumbnail.component.html'
})
export class GameThumbnailComponent {
	@Input() gameWrapper: GameWrapper;

  constructor(public navCtrl: NavController) {}

  goToGame(game) {
    this.navCtrl.push('trending-game', {game: game});
  }
}



