import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';

import { GamesPage } from './games';
import { GameThumbnailComponent } from '../../app/components/game-thumbnail/game-thumbnail.component';
import { ComponentModule } from '../../app/components/component.module';
import { PipesModule } from '../../app/pipes/pipes.module';

@NgModule({
  declarations: [
    GamesPage,
    GameThumbnailComponent
  ],
  imports: [
    IonicPageModule.forChild(GamesPage),
    ComponentModule.forRoot(),
    PipesModule.forRoot()
  ],
  providers: [
  ],
  entryComponents: [
    GamesPage
  ]
})
export class GamesPageModule {}



